* {
  margin: 0;
  padding: 0;
}
body {
  scroll-behavior: smooth;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

@media(min-width: 768px) {
  .container {
      width: 750px;
  }
}
/* Medium */
@media(min-width: 992px) {
  .container {
      width: 970px;
  }
}
/* Large  */
@media(min-width: 1200px) {
  .container {
      width: 1170px;
  }
}

body > div {
  margin-top: 65px;
}


.list-movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  row-gap: 25px;
  column-gap: 5px;
}