.card-movie {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 300px;
    .progress {
        position: absolute;
        left: 0;
        top: 0;
        height: 100px;
        width: 100px;
    }
    &:hover {
        .card-movie-content {
            height: 50%;
            .description {
                display: block;
            }
        }
    }
    img {
        width: 100%;
        height: 100%;
    }
    .card-movie-content {
        background: rgba(72, 69, 69, 0.8);
        padding: 10px 0;
        color: white;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 10%;
        transition: all .5s ease-in-out;
        overflow: hidden;
        .title {
            font-size: 16px;
            font-weight: bold;
            text-align: center;
        }
        .description {
            display: none;
            font-size: 12px;
        }
    }
    
}
